// @flow
import React, { Component } from 'react';
import { observer } from 'mobx-react';
import Bullet from "../lib/Bullet"

@observer
class BulletView extends Component {
    props: {
        bullet: Bullet
    }
    render() {
        const { currentCoordinate: { x, y }, displayOffset: { x: offsetX, y: offsetY } } = this.props.bullet
        return (
            <div className="bullet" style={{ transform: "translate(" + (x + offsetX) + "px, " + (y + offsetY) + "px)" }}></div>
        )
    }
}

export default BulletView



