// @flow
import React, { Component } from 'react';
import PropTypes from "prop-types";
import { observer } from 'mobx-react';

import DistanceCircles from "./DistanceCircles"
import UnitView from "./UnitView"
import BulletView from "./BulletView"
import Line from "./Line"

import { teams } from "../lib/gameInfo"
import Board from "../lib/Board"

@observer
class BoardView extends Component {

    props: {
        board: Board,
        children: PropTypes.element.isRequired
    }

    render() {
        const { children, board: { width, height, units, bullets } } = this.props
        return (
            <div className="board" style={{ width: width, height: height }}>
                {children}
                {units.map(unit => <DistanceCircles key={unit.id} unit={unit} />)}
                {units.map(unit => <UnitView key={unit.id} unit={unit} />)}
                {bullets.map(bullet => <BulletView key={bullet.id} bullet={bullet} />)}
                {
                    units.map(unit =>
                        <Line
                            key={unit.id}
                            unit={unit}
                            offset={unit.team === teams.red ? 2 : -2}
                            color={unit.team.colour}
                        />
                    )
                }
            </div>
        )
    }
}

export default BoardView



