// @flow
import { observable, action } from 'mobx'
import _ from 'lodash'
import Victor from 'victor' // Vector processing

import { createUnit } from "./Unit"
import Bullet from "./Bullet"
import Board from "./Board"
import Unit from "./Unit"
import { createFiringPattern } from "./FiringPattern"
import { actionTypes, directions, gameParams } from "./gameInfo"
import { metersPerSecondToPixelsPerTick, metersToPixel, pixelsPerTick, millisecondsToTick } from "./TickUtils"

class Game {
    board: Board
    tickCount: number
    ticker: number
    gameConfig: Object
    millisecondsPerTick: number

    @observable tickCount: number;
    @observable board: Board;

    constructor(config: Object) {
        this.gameConfig = config
        this.tickCount = -1
        this.ticker = 0
        this.board = new Board()

        // Place units relative to the center of the board
        const maxX = _.max(_.map(this.gameConfig.units, u => u.x));
        const maxY = _.max(_.map(this.gameConfig.units, u => u.y));
        const originX = (this.board.width / 2.0) - (maxX / 2.0);
        const originY = (this.board.height / 2.0) - (maxY / 2.0);
        for (let unitConfig of this.gameConfig.units) {
            let unit = createUnit({ ...unitConfig, ...{ x: unitConfig.x + originX, y: unitConfig.y + originY } })
            this.board.addUnit(unit)
        }
    }

    play = () => { this.changeSpeed(gameParams.millisecondsPerFrame.normal) }
    fastMotion = () => { this.changeSpeed(gameParams.millisecondsPerFrame.fast) }
    slowMotion = () => { this.changeSpeed(gameParams.millisecondsPerFrame.slow) }

    changeSpeed(millisecondsPerTick: number) {
        this.pause()
        this.start(millisecondsPerTick)
    }

    pause = () => {
        clearInterval(this.ticker);
        this.ticker = 0;
    }

    start(millisecondsPerTick: number) {
        if (!this.ticker) {
            this.millisecondsPerTick = millisecondsPerTick
            this.ticker = setInterval(() => this.tick(), this.millisecondsPerTick);
        }
    }

    @action tick() {
        this.tickCount += 1;

        // Todo: bullets shouldn't move before unit actions performed
        this.board.bullets.forEach(bullet => this.moveBullet(bullet))

        // 1. Generate modifier function for every unit, then
        // 2. Execute the modifier function
        // Modifier functions cannot reference unit data from previous ticks.
        // This ensures that modifications are only calculated based on data from previous tick
        this.board.units
            .map(unit => {
                this.fetchNextAction(unit);
                return unit.performAction()
            })
            .forEach(f => f())

        this.regenerateShield()
    }

    regenerateShield() {
        this.board.units
            .filter(u => u.lastDamageTick &&
                u.shield < u.unitType.shield &&
                u.lastDamageTick < (this.tickCount - millisecondsToTick(3000)))
            .forEach(u => { 
                let shieldIncrease = u.unitType.shield / millisecondsToTick(gameParams.shieldRechargeMilliseconds)
                u.shield += Math.min(shieldIncrease, u.unitType.shield - u.shield)
            })
    }

    fetchNextAction(unit: Unit) {
        if (this.tickCount % millisecondsToTick(250) === 0) {
            // Find next action from the unit's AI
            const newAction = unit.ai.findNextAction(unit, this.board)

            switch (newAction.actionType) {
                case actionTypes.move:
                    unit.currentAction = newAction
                    unit.performAction = this.moveUnit(unit)
                    break;
                case actionTypes.attack:
                    // If the unit is already attacking the unit then do not initiate a new attack
                    if (unit.currentAction.actionType === actionTypes.attack &&
                        newAction.target_unit === unit.currentAction.target_unit) {
                        return
                    }
                    unit.currentAction = newAction
                    unit.performAction = createFiringPattern(
                        unit,
                        unit.currentAction.target_unit,
                        this,
                        gameParams.millisecondsPerFrame.normal,
                        this.createBullet
                    )
                    break;
            }
        }
    }

    moveUnit(unit: Unit) {
        return () => {
            // Kill units when they thit the wall
            if (unit.x < 20 || unit.y < 20 || unit.x > this.board.width - 20 || unit.y > this.board.height - 20) {
                return () => this.killUnit(unit)
            }

            const { x: sourceX, y: sourceY,
                currentAction: { direction, target_unit } } = unit;

            // Do nothing if the target unit is dead
            if (!target_unit || !this.unitIsAlive(target_unit)) { return () => { } }

            const { x: targetX, y: targetY } = target_unit
            const diff = new Victor(targetX - sourceX, targetY - sourceY).normalize();
            const angle = Math.abs(diff.angleDeg()) - 90;
            const diffX = (diff.x * pixelsPerTick(unit.unitType.travelSpeed))
            const diffY = (diff.y * pixelsPerTick(unit.unitType.travelSpeed))

            return () => {
                if (direction === directions.towards) {
                    unit.x += diffX
                    unit.y += diffY
                } else if (direction === directions.away) {
                    unit.x -= diffX
                    unit.y -= diffY
                }
                unit.rotation = _.round(angle);
                unit.shootingLevel = 0
            }
        }
    }

    createBullet = (sourceUnit: Unit, targetUnit: Unit) => {
        const diff = new Victor(targetUnit.x - sourceUnit.x, targetUnit.y - sourceUnit.y).normalize();
        let bulletVelocityPixelsPerTick = metersPerSecondToPixelsPerTick(gameParams.bulletVelocity)
        const bullet = new Bullet({
            sourceUnit, targetUnit,
            sourceCoordinate: { x: sourceUnit.x, y: sourceUnit.y },
            currentCoordinate: { x: sourceUnit.x, y: sourceUnit.y },
            willHit: true,
            angle: Math.abs(diff.angleDeg()),
            velocity: { x: diff.x * bulletVelocityPixelsPerTick, y: diff.y * bulletVelocityPixelsPerTick},
            // Apply random offset to each bullet
            displayOffset: { x: parseInt(Math.random() * 10) - 5, y: parseInt(Math.random() * 10) - 5 }
        })
        this.board.bullets.push(bullet)
    }

    unitIsAlive(unit: Unit) {
        return _.includes(this.board.units, unit)
    }

    killUnit(unit: Unit) {
        unit.dead = true; // used when other objects reference this unit
        this.board.units.remove(unit)
    }

    moveBullet(bullet: Bullet) {
        const { currentCoordinate: { x, y } } = bullet;

        // Bullet has left the board
        if (x < 0 || x > this.board.width || y < 0 || y > this.board.height) {
            this.board.bullets.remove(bullet);
            return
        }

        bullet.currentCoordinate.x += bullet.velocity.x
        bullet.currentCoordinate.y += bullet.velocity.y

        if (bullet.willHit) {
            if (!this.unitIsAlive(bullet.targetUnit)) {
                // The target unit has been killed
                bullet.willHit = false;
                return;
            }
            const sourcePosition = Victor.fromObject(bullet.sourceCoordinate)
            const currentPosition = Victor.fromObject(bullet.currentCoordinate)
            const unitPosition = Victor.fromObject(bullet.targetUnit)
            const distanceTravelled = sourcePosition.distance(currentPosition)
            const distanceToUnit = sourcePosition.distance(unitPosition)

            if (distanceTravelled >= distanceToUnit) {
                // bullet has just reached the unit
                if (distanceTravelled > metersToPixel(gameParams.largeCircleRadius)) {
                    bullet.willHit = false;
                } else {
                    let chance = 0;
                    const attackRange = bullet.sourceUnit.unitType.attackRange
                    if (distanceTravelled <= metersToPixel(gameParams.smallCircleRadius)) {
                        chance = attackRange.short
                    } else if (distanceTravelled <= metersToPixel(gameParams.mediumCircleRadius)) {
                        chance = attackRange.medium
                    } else if (distanceTravelled <= metersToPixel(gameParams.largeCircleRadius)) {
                        chance = attackRange.long
                    }

                    if (Math.random() * 100 <= chance) {
                        // Bullet has hit the target
                        bullet.targetUnit.damage(bullet.sourceUnit.unitType.damagePerBullet)
                        // Record time the unit was last damaged to assist with shield regeneration
                        bullet.targetUnit.lastDamageTick = this.tickCount
                        this.board.bullets.remove(bullet);
                        if (bullet.targetUnit.hp <= 0) {
                            this.killUnit(bullet.targetUnit)
                        }
                    } else {
                        // Bullet has missed
                        bullet.willHit = false;
                    }
                }
            }
        }
    }
}

export default Game



